import React from 'react'
import { Col } from 'react-bootstrap'

const Nike = (props) => {
    const { nike } = props
    return (
        <Col md={4}>
            <img src={process.env.PUBLIC_URL + nike.imgUrl} width='100%' alt='item' />
            <h4>{nike.title}</h4>
            <p>{nike.price}</p>
        </Col>
    )
}

export default Nike