import React, { useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import Carousel from 'react-bootstrap/Carousel';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import data2 from '../data2';
import Nike from '../components/Nike';
import axios from 'axios'
import {useNavigate} from 'react-router-dom'

const Home = (props) => {

  const { cloth, setCloth, no, setNo } = props;
  const [nike, setNike] = useState(data2);
  const [count, setCount] = useState(1)
  const navigate = useNavigate();

  return (
    <>

      <section>
        {/* 슬라이더 */}
        <Carousel className='slider'>

          <Carousel.Item>
            <img src={process.env.PUBLIC_URL + "/img/slide1.png"} alt="슬라이드" width='100%' />
            <Carousel.Caption>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img src={process.env.PUBLIC_URL + "/img/slide2.png"} alt="슬라이드" width='100%' />
            <Carousel.Caption>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img src={process.env.PUBLIC_URL + "/img/slide3.png"} alt="슬라이드" width='100%' />
            <Carousel.Caption>
            </Carousel.Caption>
          </Carousel.Item>

        </Carousel>



        {/* 카테고리 */}
        <Container>
          <Row>
            <h2>SHOP CATEGORY</h2>

            <Col md={4}>
              <img src={process.env.PUBLIC_URL + "/img/center1.png"} alt="중간배너" width='100%' />
              <button>신상품</button>
            </Col>

            <Col md={4}>
              <img src={process.env.PUBLIC_URL + "/img/center2.png"} alt="중간배너" width='100%' />
              <button>기획전</button>
            </Col>

            <Col md={4}>
              <img src={process.env.PUBLIC_URL + "/img/center3.png"} alt="중간배너" width='100%' />
              <button>HOT' SALE</button>
            </Col>

          </Row>
        </Container>



        {/* 중간 배너 */}
        <Carousel className='banner'>

          <Carousel.Item>
              <img src={process.env.PUBLIC_URL + "/img/banner.png"} alt="슬라이드" width='100%' />
          </Carousel.Item>

          <Carousel.Item>
            <img src={process.env.PUBLIC_URL + "/img/banner1.png"} alt="슬라이드" width='100%' />
          </Carousel.Item>
        </Carousel>



        {/* MD`S PICK */}
        <Container className='product'>
          <h3>MD`S PICK</h3>

          <Button variant='outline-secondary'
            onClick={() => {
              let copy = [...cloth].sort((a, b) => (a.price > b.price) ? 1 : -1);
              setCloth(copy);
              let no_copy = [];
              for (let i in copy) {
                no_copy.push(copy[i].id);
              }
              setNo(no_copy);
            }}>낮은가격순 정렬</Button>

          <Button variant='outline-success'
            onClick={() => {
              let copy = [...cloth].sort((a, b) => (a.price < b.price) ? 1 : -1);
              setCloth(copy);
              let no_copy = [];
              for (let i in copy) {
                no_copy.push(copy[i].id);
              }
              setNo(no_copy);
            }}>높은가격순 정렬</Button>

          <Row>
            {
              cloth.map((cloth, i) =>
                <Col md={3} key={i} onClick={()=>{
                  navigate('/detail/'+no[i])
                }}>
                  <img src={process.env.PUBLIC_URL + cloth.imgUrl} width='100%' />
                  <h4>{cloth.title}</h4>
                  {/* <p>{cloth.price}</p> */}
                </Col>
              )
            }
          </Row>
        </Container>



        {/* 이벤트페이지 */}
        <>
          <div className='event-font'>
            <h3>PALACE INSTAGRAM</h3>
            <p>PALACE 공식 인스타그램으로 다양한 소식을 만나보세요</p>
          </div>

          <Swiper
            slidesPerView={3}
            spaceBetween={10}
            grabCursor={true}
            pagination={{ clickable: true, }}
            breakpoints={{
              500: {
                slidesPerView: 1,
                spaceBetween: 5,
              },
              768: {
                slidesPerView: 2,
                spaceBetween: 5,
              },
              1024: {
                slidesPerView: 3,
                spaceBetween: 5,
              },
            }}
            className="mySwiper mt-5"
          >
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event1.jpg"} alt="event1" />
            </SwiperSlide>

            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event2.jpg"} alt="event2" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event3.jpg"} alt="event3" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event4.jpg"} alt="event4" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event5.jpg"} alt="event5" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event6.jpg"} alt="event6" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + "/img/event7.jpg"} alt="event7" />
            </SwiperSlide>
          </Swiper>
        </>



        {/* 상품더보기 */}
        <Container className='other'>
          <h3>NEW PRODUCT</h3>
          <Row>
            {
              nike.map((item, i) =>
                <Nike nike={item} key={i} />
              )
            }
          </Row>
          <Button variant="outline-success" onClick={() => {

            if (count === 1) {
              axios.get('https://jeonjongho97.github.io/react_data/nike2.json')
                .then((result) => {                
                  let copy = [...nike, ...result.data];            
                  setNike(copy);
                  setCount(2);
                })

            } else if (count === 2) {
              axios.get('https://jeonjongho97.github.io/react_data/nike3.json')
                .then((result) => {
                  let copy = [...nike, ...result.data];
                  setNike(copy);
                  setCount(3);
                })
            } else {
              alert('더 이상 상품이 없습니다.')
            }
          }}>+3개 더보기</Button>{''}

        </Container>

      </section>

    </>
  )
}

export default Home