import React from 'react'
import { Table, Container, Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { addCount,removeCount } from '../store';



const Cart = (props) => {

    const cart = useSelector((state => { return state.cart }))
    console.log(cart)
    const dispatch = useDispatch()

    function sum(){
        let total = 0;
        for(let a=0; a<cart.length; a++){
            let b= cart[a].price*cart[a].amount;
            total+=b;
        }
        return total;
    }

    return (
        <>

        <div className='back'>
            <h3 className='back-text'>SHOPPING CART</h3>
        </div>



        <Container>
            <Table className='mt-5'>
                <thead>
                    <tr>
                        <th>상품</th>
                        <th>상품명</th>
                        <th>가격</th>
                        <th>수량</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cart.map((cloth, i) =>
                            <tr key={i}>
                                <td className='cart-line'><img src={cloth.imgUrl} alt="이미지" width='50px' /></td>
                                <td className='cart-line'>{cloth.title}</td>
                                <td className='cart-line'>{cloth.price}</td>
                                <td className='cart-line'>
                                    <button className='plus' onClick={() => dispatch(addCount(cloth.id))}>+</button>
                                    <span>{cloth.amount}</span>
                                    <button className='plus' onClick={() => dispatch(removeCount(cloth.id))}>-</button>
                                </td>

                            </tr>
                        )
                    }
                </tbody>
            </Table>

            <div className='total'>
                <div className='total-price'>TOTAL = </div>
                <div className='total-other'>{sum()}</div>

            </div>

            <Button className='order' variant='dark'>주문하기</Button>
            <div></div>
        </Container>
        </>
    )
}

export default Cart