import { configureStore, createSlice } from '@reduxjs/toolkit'


const user = createSlice({
    name: 'user',
    initialState: { name: 'kim', age: 20 },
    reducers: {
        changeName(state) {
            state.name = 'john kim'
        },

        increase(state, action) {
            state.age += action.payload
        }
    }
})
export const { changeName, increase } = user.actions





const cart = createSlice({
    name: 'cart',
    initialState: [],
    reducers: {
        removeCount(state,action){
            const num = state.findIndex(a => a.id ===action.payload)
            state[num].amount--
            if (state[num].amount < 1){
                alert('최소 주문 수량은 1개 입니다.');
                state[num].amount = 1;
            }
        },

        addCount(state, action) {

            let idx = state.findIndex((a) => { return a.id === action.payload })
            ++state[idx].amount;

        },

        removeItem(state, action){
            const num = state.findIndex(a => a.id === action.payload)
            state.splice(num, 1)
        },

        sortName(state) {
            state.sort((a, b) =>
                a.item > b.item ? 1 : -1,
            );
        },

        addItem(state, action) {            
            const num = state.findIndex( a => a.id === action.payload.id)
            if (num >= 0) {
                state[num].amount++;
            }else {
                state.push(action.payload);
            }           
        }
    }
})

export const { addCount, sortName, addItem, removeCount, removeItem } = cart.actions


export default configureStore({
    reducer: {
        user: user.reducer,
        cart: cart.reducer
    }
})